<template>
    <div class="iframebox">
        <iframe :src="iframeUrl" frameborder="0" :style="{height:iframHeight}"></iframe>
    </div>
</template>
<script>
export default {
    name:'iframes_mine',
    data(){
        return{
            iframHeight:'',
            iframeUrl: this.$route.query.iframeUrl
        }
    },
    mounted(){
        this.iframHeight = window.innerHeight+'px';
        console.log(this.$route.query.iframeUrl);
    },
    methods:{}
}
</script>



